import React, { Fragment } from "react";
import Input from "../Inputs/Input";
import { useTranslation } from "react-i18next";
import { Grid, Typography, Box } from "@material-ui/core";
import SelectInput from "../Inputs/SelectInput";
import { DateTimePicker, MuiPickersUtilsProvider } from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";
import arLocale from "date-fns/locale/ar-SA";
import i18next from "../../i18n";
import { isError } from "../../util/generals";
import IosSwitch from "../Inputs/IosSwitch";

const CouponInfo = ({ getResult, state, touched, coupons }) => {
  const { t } = useTranslation();

  return (
    <Fragment>
      <Box className="flex flex-justify-end">
        <IosSwitch
          state={state.status.value}
          onChange={getResult}
          name="status"
        />
      </Box>
      <Typography
        component="div"
        className="center p"
        variant="caption"
        color="textSecondary"
      >
        {t("common:required_star")}
      </Typography>
      <Grid container spacing={2}>
        <Grid item md={4} sm={6} xs={12}>
          <Input
            label={t("common:name") + " **"}
            initialValue={state.title.value}
            name="title"
            getResult={getResult}
            required
            min={3}
            max={100}
            setTouched={touched}
            serverError={isError(
              coupons.errors && coupons.errors.data,
              "title"
            )}
          />
        </Grid>
        <Grid item md={4} sm={6} xs={12}>
          <Input
            label={t("coupons:code") + " **"}
            initialValue={state.code.value}
            name="code"
            getResult={getResult}
            required
            min={2}
            max={11}
            setTouched={touched}
            serverError={isError(coupons.errors && coupons.errors.data, "code")}
          />
        </Grid>
        <Grid item md={4} sm={6} xs={12}>
          <Input
            label={t("coupons:amount") + " **"}
            initialValue={state.amount.value}
            name="amount"
            getResult={getResult}
            required
            decimal
            mathMin="0"
            setTouched={touched}
            serverError={isError(
              coupons.errors && coupons.errors.data,
              "amount"
            )}
          />
        </Grid>
        <Grid item md={4} sm={6} xs={12}>
          <SelectInput
            options={[
              { value: "P", text: t("coupons:P") },
              { value: "F", text: t("coupons:F") },
            ]}
            initialValue={state.type.value}
            name="type"
            label={t("coupons:type") + " **"}
            getResult={getResult}
            serverError={isError(coupons.errors && coupons.errors.data, "type")}
          />
        </Grid>
        <Grid item md={4} sm={6} xs={12}>
          <MuiPickersUtilsProvider
            utils={DateFnsUtils}
            locale={i18next.language === "ar" ? arLocale : ""}
          >
            <DateTimePicker
              value={state.date_start.value}
              label={t("coupons:date_start")}
              onChange={(e) => getResult(e, true, "date_start")}
              style={{ width: "100%" }}
              inputVariant="outlined"
              cancelLabel={t("common:cancel")}
              okLabel={t("common:confirm")}
              format="dd-MMMM-yyyy hh:mm:ss aaaa"
            />
          </MuiPickersUtilsProvider>
        </Grid>
        <Grid item md={4} sm={6} xs={12}>
          <MuiPickersUtilsProvider
            utils={DateFnsUtils}
            locale={i18next.language === "ar" ? arLocale : ""}
          >
            <DateTimePicker
              value={state.date_end.value}
              label={t("coupons:date_end")}
              onChange={(e) => getResult(e, true, "date_end")}
              style={{ width: "100%" }}
              inputVariant="outlined"
              cancelLabel={t("common:cancel")}
              okLabel={t("common:confirm")}
              format="dd-MMMM-yyyy hh:mm:ss aaaa"
            />
          </MuiPickersUtilsProvider>
        </Grid>
        <Grid item md={4} sm={6} xs={12}>
          <Input
            label={t("coupons:total_limit")}
            initialValue={state.total_limit.value}
            name="total_limit"
            getResult={getResult}
            decimal
            setTouched={touched}
            serverError={isError(
              coupons.errors && coupons.errors.data,
              "total_limit"
            )}
            helperNotice={t("coupons:total_limit_notice")}
          />
        </Grid>
        <Grid item md={4} sm={6} xs={12}>
          <Input
            label={t("coupons:user_limit")}
            initialValue={state.user_limit.value}
            name="user_limit"
            getResult={getResult}
            decimal
            setTouched={touched}
            serverError={isError(
              coupons.errors && coupons.errors.data,
              "user_limit"
            )}
            helperNotice={t("coupons:user_limit_notice")}
          />
        </Grid>
        <Grid item md={4} sm={6} xs={12}>
          <Input
            label={t("coupons:min_total")}
            initialValue={state.min_total.value}
            name="min_total"
            getResult={getResult}
            decimal
            setTouched={touched}
            serverError={isError(
              coupons.errors && coupons.errors.data,
              "min_total"
            )}
            helperNotice={t("coupons:min_total_notice")}
          />
        </Grid>
      </Grid>
    </Fragment>
  );
};

export default CouponInfo;
