import React, { useState, useCallback, useEffect, Fragment } from "react";
import { useSelector, useDispatch } from "react-redux";
import { useTranslation } from "react-i18next";
import * as actions from "../../store/actions";
import { Typography, Paper, Box, Button } from "@material-ui/core";
import TableSkeleton from "../../components/common/TableSkeleton";
import CouponInfo from "../../components/Coupons/CouponInfo";
import AddRoundedIcon from "@material-ui/icons/AddRounded";
import EditRoundedIcon from "@material-ui/icons/EditRounded";
import ButtonSpinner from "../../components/UI/Spinners/ButtonSpinner";
import CouponProdCat from "../../components/Coupons/CouponProdCat";

const initialState = (coupon) => {
  return {
    title: {
      value: coupon ? coupon.title : "",
      validity: coupon ? !!coupon.title : false,
    },
    code: {
      value: coupon ? coupon.code : "",
      validity: coupon ? !!coupon.code : false,
    },
    type: {
      value: coupon ? coupon.type : "P",
      validity: true,
    },
    amount: {
      value: coupon ? coupon.amount : 0,
      validity: coupon ? !!coupon.amount : false,
    },
    min_total: {
      value: coupon ? coupon.min_total : "0",
      validity: true,
    },
    date_start: {
      value: coupon ? new Date(coupon.date_start) : new Date(),
      validity: true,
    },
    date_end: {
      value: coupon
        ? new Date(coupon.date_end)
        : new Date(Date.now() + 60 * 60 * 24 * 10 * 1000),
      validity: true,
    },
    total_limit: {
      value: coupon ? coupon.total_limit : "0",
      validity: true,
    },
    user_limit: {
      value: coupon ? coupon.user_limit : "0",
      validity: true,
    },
    status: {
      value: coupon ? coupon.status : "1",
      validity: true,
    },
    products: {
      value: coupon ? coupon.products.map((c) => c.product_id) : [],
      validity: true,
    },
    categories: {
      value: coupon ? coupon.categories.map((c) => c.category_id) : [],
      validity: true,
    },
  };
};

let timeout;

const AddEdit = ({ history, match }) => {
  const isEdit = match.params.id;
  const [editLoading, setEditLoading] = useState(!!isEdit);
  const [state, setState] = useState(initialState());
  const [touched, setTouched] = useState(false);
  const coupons = useSelector((state) => state.coupons);
  const { t } = useTranslation();
  const dispatch = useDispatch();

  const fetchCoupon = useCallback(async () => {
    try {
      const fetchedCoupon = await dispatch(actions.getCoupon(isEdit));
      setState(initialState(fetchedCoupon));
      setEditLoading(false);
    } catch (err) {
      let msg = err.message || t("coupons:coupon_not_found");
      dispatch(actions.fireSnake("error", msg));
      history.replace("/coupons");
    }
  }, [isEdit, dispatch, t, history]);

  useEffect(() => {
    if (isEdit) {
      timeout = setTimeout(() => {
        fetchCoupon();
      }, 0);
    }
    return () => clearTimeout(timeout);
  }, [isEdit, fetchCoupon]);

  const handleCouponState = (value, validity, name) => {
    setState((prev) => {
      return {
        ...prev,
        [name]: { value: value, validity: validity },
      };
    });
  };
  const handleCatProductState = (value, validity, name) => {
    setState((prev) => {
      let updated = [];
      if (value.length) {
        const exist = prev[name].value.find((v) => v === +value);
        updated = exist
          ? prev[name].value.filter((v) => v !== +value)
          : prev[name].value.concat(+value);
      }
      return {
        ...prev,
        [name]: {
          value: updated,
          validity: validity,
        },
      };
    });
  };
  const handleSubmit = async () => {
    let body = {};

    for (const key in state) {
      if (key !== "description") {
        body = { ...body, [key]: state[key].value };
        if (!state[key].validity) {
          setTouched(true);
          return dispatch(
            actions.fireSnake("error", t("common:complete_red_input"))
          );
        }
      }
    }

    try {
      await dispatch(actions.addEditCoupon(body, isEdit));
      history.replace("/coupons");
    } catch (err) {
      let msg = err.message || t("coupons:save_err");
      dispatch(actions.fireSnake("error", msg));
    }
  };

  return (
    <Fragment>
      <Typography align="center" variant="h4" className="py-1">
        {isEdit ? t("coupons:edit_coupon") : t("coupons:add_coupon")}
      </Typography>
      {editLoading ? (
        <Paper>
          <TableSkeleton row={4} col={2} />
        </Paper>
      ) : (
        <Fragment>
          {/* Coupon Info */}
          <Paper className="px-1">
            <CouponInfo
              state={state}
              getResult={handleCouponState}
              coupons={coupons}
              touched={touched}
            />
          </Paper>

          {/* Product Cat/Filter Section */}
          <Paper className="my-1 p-1 center">
            <Typography variant="h6" className="p">
              {t("coupons:coupon_cat_prod")}
            </Typography>
            <CouponProdCat
              categories={state.categories.value}
              products={state.products.value}
              getResult={handleCatProductState}
            />
          </Paper>

          {/* Submit */}
          <Box className="center py-1">
            <Button
              variant="contained"
              color="primary"
              size="large"
              onClick={handleSubmit}
              endIcon={coupons.loading && <ButtonSpinner />}
              startIcon={isEdit ? <EditRoundedIcon /> : <AddRoundedIcon />}
              disabled={coupons.loading}
              style={{ width: "80%" }}
            >
              <Typography variant="h4" className="py-1">
                {isEdit ? t("coupons:edit_coupon") : t("coupons:add_coupon")}
              </Typography>
            </Button>
          </Box>
        </Fragment>
      )}
    </Fragment>
  );
};

export default AddEdit;
