import React, { useState, useEffect, useCallback, useRef } from "react";
import {
  FormControl,
  FormHelperText,
  ListSubheader,
  Select,
} from "@material-ui/core";
import InputLabel from "@material-ui/core/InputLabel";
import { useTranslation } from "react-i18next";

const SelectNative = (props) => {
  const {
    options,
    name,
    required,
    getResult,
    label,
    errMsg,
    disabled,
    initialValue,
    setTouched,
    helperText,
    serverError,
    variant,
    loading,
    multiple,
    // placeholder,
  } = props;

  const [state, setState] = useState({
    value: initialValue,
    touched: false,
    valid: false,
    errorMsg: "",
  });
  const { t } = useTranslation();
  const inputLabel = useRef(null);
  const [labelWidth, setLabelWidth] = useState(0);
  useEffect(() => {
    if (label) {
      setLabelWidth(inputLabel.current.offsetWidth);
    }
  }, [label]);
  useEffect(() => {
    if (setTouched && state.value === "") {
      setState({ ...state, touched: true });
    }
    // eslint-disable-next-line
  }, [setTouched]);
  const showServerErr = useCallback(() => {
    setState({
      ...state,
      valid: false,
      touched: true,
      errorMsg: serverError,
    });
    // eslint-disable-next-line
  }, [serverError]);
  useEffect(() => {
    if (serverError) {
      showServerErr();
    }
  }, [serverError, showServerErr]);
  const handleTouched = () => {
    if (!state.touched) {
      setState({ ...state, touched: true, errorMsg: required ? errMsg : "" });
    }
  };
  const handleChange = (e) => {
    // if (multiple) {
    //   let options = e.target.options;
    //   const results = [];

    //   for (let i = 0, l = options.length; i < l; i++) {
    //     if (options[i].selected) {
    //       results.push(options[i].value);
    //     }
    //   }
    //   let isValid = required ? !!results.length : true;
    //   setState({ ...state, value: results, valid: isValid });
    //   getResult(results, isValid, name);
    // } else {
    //   let val = e.target.value;
    //   let isValid = required ? !!val : true;

    //   setState({ ...state, value: val, valid: isValid });
    //   getResult(val, isValid, name);
    // }
    let val = e.target.value;
    let isValid = required ? !!val : true;
    if (multiple) {
      isValid = required ? !!val.length : true;
      if (val) {
        setState((prev) => {
          const exist = prev.value.find((v) => v === +val);
          const updated = exist
            ? prev.value.filter((v) => v !== +val)
            : prev.value.concat(+val);

          return {
            ...prev,
            value: updated,
            valid: isValid,
          };
        });
      } else {
        val = [];
        setState({ ...state, value: val, valid: isValid });
      }
    } else {
      setState({ ...state, value: val, valid: isValid });
    }
    getResult(val, isValid, name);
  };

  return (
    <>
      <FormControl
        variant={variant || "outlined"}
        fullWidth
        error={
          (required && state.touched && !state.valid && !initialValue) ||
          (serverError && true)
        }
        // style={{ minWidth: 230 }}
        disabled={disabled}
      >
        {label && (
          <InputLabel ref={inputLabel} focused shrink>
            {label}
          </InputLabel>
        )}
        <Select
          value={state.value}
          onBlur={handleTouched}
          onChange={handleChange}
          labelWidth={labelWidth}
          // IconComponent={loading ? SelectSpinner : ExpandMoreIcon}
          multiple={multiple}
          native
        >
          {loading ? (
            <option>{t("common:loadingggg")}</option>
          ) : (
            options.map((op) =>
              op.subheader ? (
                <ListSubheader
                  value=""
                  key={op.value}
                  className="bolder center"
                >
                  {op.text}
                </ListSubheader>
              ) : (
                op.value !== null && (
                  <option value={op.value} key={op.value}>
                    {op.text}
                  </option>
                )
              )
            )
          )}
        </Select>
      </FormControl>
      <FormHelperText style={{ color: "#931621" }}>
        {state.touched && !state.valid && !initialValue && state.errorMsg}
      </FormHelperText>
      {serverError && initialValue && (
        <FormHelperText style={{ color: "#931621" }}>
          {serverError}
        </FormHelperText>
      )}
      {helperText && <FormHelperText>{helperText}</FormHelperText>}
    </>
  );
};

export default SelectNative;
