import React, { Fragment, useEffect, useCallback, useState } from "react";
import { useTranslation } from "react-i18next";
import { Grid, LinearProgress, Box } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import {
  categoriesAutocomplete,
  productsAutocomplete,
} from "../../store/actions";
import SelectNative from "../Inputs/SelectNative";
import Input from "../Inputs/Input";

const CouponProdCat = ({ getResult, categories, products }) => {
  const { t } = useTranslation();
  const [cats, setCats] = useState([]);
  const [prods, setProds] = useState([]);
  const prodState = useSelector((state) => state.products);
  const catState = useSelector((state) => state.categories);
  const dispatch = useDispatch();

  const fetchCats = useCallback(
    async (q) => {
      try {
        const _cats = await dispatch(categoriesAutocomplete(q));
        setCats(_cats);
      } catch (err) {
        //
      }
    },
    [dispatch]
  );

  const fetchProds = useCallback(
    async (q) => {
      try {
        const _products = await dispatch(productsAutocomplete(q));
        setProds(_products);
      } catch (err) {
        //
      }
    },
    [dispatch]
  );

  useEffect(() => {
    fetchCats();
    fetchProds();
  }, [fetchCats, fetchProds]);

  return (
    <Fragment>
      <Grid container spacing={2}>
        <Grid item sm={6} xs={12}>
          <Box className="py">
            <Input
              getResult={fetchCats}
              label={t("coupons:search_categories")}
            />
            {catState.loading && <Loader />}
          </Box>
          <SelectNative
            options={cats.map((c) => {
              return { value: c.category_id, text: c.title };
            })}
            name="categories"
            label={t("common:categories")}
            getResult={getResult}
            initialValue={categories}
            multiple
          />
        </Grid>
        <Grid item sm={6} xs={12}>
          <Box className="py">
            <Input
              getResult={fetchProds}
              label={t("coupons:search_products")}
            />
            {prodState.loading && <Loader />}
          </Box>
          <SelectNative
            options={prods.map((p) => {
              return { value: p.product_id, text: p.title };
            })}
            name="products"
            label={t("common:products")}
            getResult={getResult}
            initialValue={products}
            multiple
          />
        </Grid>
      </Grid>
    </Fragment>
  );
};

const Loader = () => {
  return (
    <LinearProgress
      style={{ borderRadius: 5, marginLeft: 1, marginRight: 1 }}
    />
  );
};

export default CouponProdCat;
